.card-image {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 3px;
  border: 1px solid #444;
  margin-bottom: 3px;
}

.knife-name {
  display: block;
  text-align: center;
  text-transform: uppercase;
  font-family: monospace;
  font-weight: bold;
  margin-bottom: 3px;
}

.main-description,
.verbose-description {
  font-size: 0.75em;
  font-family: monospace;
  line-height: 2em;
}

.main-description {
  margin-bottom: 1em;
  text-align: justify;
}

.verbose-description div {
  display: inline;
  padding: 0;
  margin: 0;
}

.two-entry-style {
  display: none;
}
